





























import { Component, Vue, Watch } from 'vue-property-decorator';
import ThirdLogin from '@/components/ThirdLogin.vue';
import LoginInput from '@/components/LoginInput.vue';
import loginHttp from '../../http/login';
import myHttp from '../../http/my';
import cookie from '@/http/cookie';

@Component({
    components: {
        LoginInput,
        ThirdLogin
    }
})
export default class Login extends Vue {
    private user = {
        account: '',
        password: ''
    };
    private message: boolean | string = false;
    private inputs!: any;
    // @Watch('user', { deep: true }) onuserChange(val: string, oldVal: string) {
    //     console.log(this.user.account);
    //     this.$nextTick(function() {
    //         this.user.account = this.user.account
    //             .replace(/[^0-9]/g, '')
    //             .slice(0, 11);
    //     });
    //     this.message = false;
    // }

    @Watch('message') onMessageChanged(val: string | boolean) {
        console.log(val);
        if (val !== '') {
            let timeout;
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                this.message = '';
            }, 3000);
        }
    }

    private login() {
        const focus: any = document.activeElement;
        if (this.user.account.length < 11) {
            this.message = '账号格式错误';
        } else if (this.user.password.length < 6 && this.inputs[0] === focus) {
            this.inputs[1].focus();
            return false;
        } else if (this.user.password.length < 6) {
            this.message = '账号或密码错误';
        } else {
            focus.blur();
        }
        loginHttp.postLogin(this.user).then(res => {
            console.log(res);
            if (res.data.code === 200) {
                localStorage.setItem('self', JSON.stringify(res.data.data));
                cookie.set('HomeToken', res.headers.hometoken, 7);
                this.$router.push('home');
            } else {
                this.message = res.data.message;
            }
        });
    }

    private mounted() {
        this.inputs = document.querySelectorAll('input');
    }
}
